<template>
  <div id="loading-container" class="vs-con-loading__container">
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <form-wizard ref="wizard"
                   color="rgba(var(--vs-primary), 1)"
                   :title="null" :subtitle="null"
                   :nextButtonText="$t('NEXT')"
                   :backButtonText="$t('PREVIOUS')">

        <template slot="custom-buttons-left" slot-scope="props">
          <vs-button class="mr-2" color="primary" type="border" @click="cancel" v-if="props.activeTabIndex === 0">{{ $t('CANCEL') }}</vs-button>
        </template>
        <template slot="finish">
          <div class="dropdown-button-container">
            <vs-button class="btnx" type="filled" @click="modifyParty('browse')">{{ $t('SUBMIT') }}</vs-button>
            <vs-dropdown vs-trigger-click>
              <vs-button class="btn-drop" type="filled" icon="fa-angle-down" icon-pack="far"></vs-button>
              <vs-dropdown-menu>
                <vs-dropdown-item @click="modifyParty('view')">
                  <span>{{ $t('SUBMIT_VIEW') }}</span>
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
          </div>
        </template>

        <tab-content :title="$t('PROFILE')" icon="far fa-file-alt" :before-change="loadCustomer">
          <div class="nv-row mb-4">
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('PSEUDO_ID') }}</label>
              <vs-input class="w-full" v-model="party.pseudoId"/>
            </div>
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('EXTERNAL_ID') }}</label>
              <vs-input class="w-full" v-model="party.externalId"/>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col w-full">
              <label class="vs-input--label">{{ $t('COMMENTS') }}</label>
              <vs-textarea class="w-full" v-model="party.comments"/>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col sm:w-1/2 w-full">
              <vs-checkbox v-model="party.disabled">{{ $t('DISABLED') }}</vs-checkbox>
            </div>
          </div>
        </tab-content>

        <tab-content :title="$t('DETAILS')" icon="far fa-user">
          <div v-if="party.partyTypeEnumId === 'PtyPerson'">
            <div class="nv-row mb-4">
              <div class="nv-col sm:w w-full">
                <div class="flex justify-end">
                  <span><i class="far fa-male fa-lg"></i></span>
                  <vs-switch class="mx-2" v-model="genderSwitch" color="#FF69B4"/>
                  <span><i class="far fa-female fa-lg"></i></span>
                </div>
              </div>
            </div>
            <div class="nv-row mb-4">
              <div class="nv-col sm:w-1/2 w-full">
                <label class="vs-input--label">{{ $t('SALUTATION') }}</label>
                <vs-input class="w-full" v-model="party.salutation"/>
              </div>
              <div class="nv-col sm:w-1/2 w-full required">
                <label class="vs-input--label">{{ $t('FIRST_NAME') }}</label>
                <vs-input class="w-full" v-model="party.firstName"/>
              </div>
            </div>
            <div class="nv-row mb-4">
              <div class="nv-col sm:w-1/2 w-full">
                <label class="vs-input--label">{{ $t('MIDDLE_NAME') }}</label>
                <vs-input class="w-full" v-model="party.middleName"/>
              </div>
              <div class="nv-col sm:w-1/2 w-full required">
                <label class="vs-input--label">{{ $t('LAST_NAME') }}</label>
                <vs-input class="w-full" v-model="party.lastName"/>
              </div>
            </div>
            <div class="nv-row mb-4">
              <div class="nv-col sm:w-1/2 w-full">
                <label class="vs-input--label">{{ $t('SUFFIX') }}</label>
                <vs-input class="w-full" v-model="party.suffix"/>
              </div>
              <div class="nv-col sm:w-1/2 w-full">
                <label class="vs-input--label">{{ $t('MAIDEN_NAME') }}</label>
                <vs-input class="w-full" v-model="party.mothersMaidenName"/>
              </div>
            </div>
            <div class="nv-row mb-4">
              <div class="nv-col sm:w-1/2 w-full">
                <label class="vs-input--label">{{ $t('BIRTH_DATE') }}</label>
                <nv-input-group>
                  <template slot="append">
                    <div class="append-text bg-dark">
                      <i class="far fa-calendar-alt"></i>
                    </div>
                  </template>
                  <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="party.birthDate" :format="dateFormat"></datepicker>
                </nv-input-group>
              </div>
              <div class="nv-col sm:w-1/2 w-full">
                <label class="vs-input--label">{{ $t('DECEASED_DATE') }}</label>
                <nv-input-group>
                  <template slot="append">
                    <div class="append-text bg-dark">
                      <i class="far fa-calendar-alt"></i>
                    </div>
                  </template>
                  <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="party.deceasedDate" :format="dateFormat"></datepicker>
                </nv-input-group>
              </div>
            </div>
            <div class="nv-row mb-4">
              <div class="nv-col sm:w-1/2 w-full">
                <label class="vs-input--label">{{ $t('MARITAL_STATUS') }}</label>
                <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="party.maritalStatusEnumId">
                  <vs-select-item v-for="(enumeration, index) in maritalStatuses" :key="index" :value="enumeration.enumId" :text="enumeration.description"/>
                </vs-select>
              </div>
              <div class="nv-col sm:w-1/2 w-full">
                <label class="vs-input--label">{{ $t('RESIDENCE_STATUS') }}</label>
                <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="party.residenceStatusEnumId">
                  <vs-select-item v-for="(enumeration, index) in residenceStatuses" :key="index" :value="enumeration.enumId" :text="enumeration.description"/>
                </vs-select>
              </div>
            </div>
            <div class="nv-row mb-4">
              <div class="nv-col sm:w-1/2 w-full">
                <label class="vs-input--label">{{ $t('EMPLOYMENT_STATUS') }}</label>
                <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="party.employmentStatusEnumId">
                  <vs-select-item v-for="(enumeration, index) in employmentStatuses" :key="index" :value="enumeration.enumId" :text="enumeration.description"/>
                </vs-select>
              </div>
              <div class="nv-col sm:w-1/2 w-full">
                <label class="vs-input--label">{{ $t('OCCUPATION') }}</label>
                <vs-input class="w-full" v-model="party.occupation"/>
              </div>
            </div>
          </div>
          <div v-if="party.partyTypeEnumId === 'PtyOrganization'">
            <div class="nv-row mb-4">
              <div class="nv-col sm:w w-full required">
                <label class="vs-input--label">{{ $t('ORGANIZATION_NAME') }}</label>
                <vs-input class="w-full" v-model="party.organizationName"/>
              </div>
            </div>
            <div class="nv-row mb-4">
              <div class="nv-col sm:w-1/2 w-full">
                <label class="vs-input--label">{{ $t('NUMBER_OF_EMPLOYEES') }}</label>
                <vs-input type="number" class="w-full" v-model="party.numEmployees"/>
              </div>
              <div class="nv-col sm:w-1/2 w-full">
                <label class="vs-input--label">{{ $t('ANNUAL_REVENUE') }}</label>
                <vs-input type="number" class="w-full" v-model="party.annualRevenue"/>
              </div>
            </div>
          </div>
          <div v-if="party.partyTypeEnumId === 'PtyAutomatedAgent'">
            <vs-alert active="true" color="warning" class="mb-5">
              <p>{{ $t('NOTHING_MORE') }}</p>
            </vs-alert>
          </div>
        </tab-content>
      </form-wizard>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import DatePickerMixin from '@/mixins/commons/DatePickerMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import PartyService from '@/services/nucleus/PartyService'
import EnumerationService from '@/services/commons/EnumerationService'
import { FormWizard, TabContent } from 'vue-form-wizard'
import Datepicker from 'vuejs-datepicker'

export default {
  name: 'party-modify',
  mixins: [AlertMixin, DatePickerMixin, LoadingMixin],
  components: {
    FormWizard,
    TabContent,
    Datepicker
  },
  data: function () {
    return {
      party: {},
      loadedExternalId: '',
      maritalStatuses: [],
      employmentStatuses: [],
      residenceStatuses: [],
      genderSwitch: false
    }
  },
  methods: {
    async load () {
      let self = this
      PartyService.parties({
        partyId: this.$route.params.id
      }).then(function (response) {
        self.party = response.data.partyList[0] || {}
        self.party.disabled = self.party.disabled === 'Y'
        self.genderSwitch = self.party.gender === 'F'
        self.loadedExternalId = self.party.externalId
      })

      EnumerationService.enumerations({
        enumTypeId: 'MaritalStatus',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.maritalStatuses = response.data.enumerationList || []
      })

      EnumerationService.enumerations({
        enumTypeId: 'ResidenceStatus',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.residenceStatuses = response.data.enumerationList || []
      })

      EnumerationService.enumerations({
        enumTypeId: 'EmploymentStatus',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.employmentStatuses = response.data.enumerationList || []
      })
    },
    loadCustomer () {
      if (this.loadedExternalId !== this.party.externalId && this.party.externalId !== '') {
        let self = this
        this.startLoading('loading-container')
        PartyService.customer({
          customerId: this.party.externalId
        }).then(function (response) {
          self.stopLoading('loading-container')
          let customer = response.data.customer || {}
          self.party.firstName = customer.firstName || ''
          self.party.lastName = customer.lastName || ''
          self.party.organizationName = (self.party.firstName + ' ' + self.party.lastName).trim()
        }).catch(function (error) {
          self.stopLoading('loading-container')
          self.showDangerAlert(error.response.data.errors)
        })
      }
      return true
    },
    modifyParty (nextAction) {
      let self = this
      this.startLoading('loading-container')
      PartyService.modifyParty({
        partyId: this.$route.params.id,
        pseudoId: self.party.pseudoId,
        externalId: self.party.externalId,
        comments: self.party.comments,
        disabled: self.party.disabled ? 'Y' : 'N',
        gender: self.party.gender,
        salutation: self.party.salutation,
        firstName: self.party.firstName,
        middleName: self.party.middleName,
        lastName: self.party.lastName,
        suffix: self.party.suffix,
        mothersMaidenName: self.party.mothersMaidenName,
        birthDate: self.party.birthDate,
        deceasedDate: self.party.deceasedDate,
        maritalStatusEnumId: self.party.maritalStatusEnumId,
        residenceStatusEnumId: self.party.residenceStatusEnumId,
        employmentStatusEnumId: self.party.employmentStatusEnumId,
        occupation: self.party.occupation,
        organizationName: self.party.organizationName,
        numEmployees: self.party.numEmployees,
        annualRevenue: self.party.annualRevenue
      }).then(function (response) {
        self.stopLoading('loading-container')
        if (response.status === 200) {
          if (nextAction === 'view') {
            self.$router.push({ name: 'ViewParty', params: { id: response.data.partyId } })
          } else {
            self.$router.push({ name: 'BrowseParty' })
          }
        }
      }).catch(function (error) {
        self.stopLoading('loading-container')
        self.showDangerAlert(error.response.data.errors)
      })
    },
    toggleGenderType (gender) {
      this.gender = gender
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted () {
    this.load()
  },
  watch: {
    genderSwitch: function () {
      this.party.gender = this.genderSwitch ? 'F' : 'M'
    }
  }
}
</script>
